<template>
    <el-container>
        <el-aside>
            <el-col >
                <h5>{{usrnm}}</h5>
                <el-menu
                    default-active="2"
                    class="el-menu-vertical-demo"
                    >
                    <el-menu-item index="1" @click="change(-1)">
                        <i class="el-icon-location"></i>
                        <span>我的小组</span>
                    </el-menu-item>
                    <el-menu-item :index="(index+'')" v-for="(item,index) in mygroup" :key="index" @click="change(index)">
                        <span >{{item.name}}</span>
                    </el-menu-item>
                <el-menu-item index="2" @click="cardClick('/groups')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">热门小组</span>
                </el-menu-item>
                <el-menu-item index="3" @click="cardClick('/')">
                    <i class="el-icon-setting"></i>
                    <span slot="title">返回首页</span>
                </el-menu-item>
                </el-menu>
            </el-col>
        </el-aside>
        <el-main v-if="status == -1">
            <el-header>各组热点</el-header>
            <el-row :span="8" v-for="(o, index) in topic" :key="index" :offset="index > 0 ? 2 : 0">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>{{o.name}}</span>
                        <el-button style="float: right; padding: 3px 0" type="text" @click="change(index)">进去康康</el-button>
                    </div>
                    <div class="text item">
                        <img :src=o.url class="image">
                        <span>{{o.short}}</span>
                    </div>
                </el-card>
                <div style="height:20px;"></div>
            </el-row>
        </el-main>
        <el-main v-else-if="!isTopic">
            <el-header>{{mygroup[status].name}} 话题</el-header>
            <el-button type="primary"  class="com" @click="manage(mygroup[status].gpno)">
                申请成为管理员
            </el-button>
            <el-row :span="8"  v-for="(o, index) in this.post[this.status]" :key="index" :offset="index > 0 ? 2 : 0">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>{{o.name}}</span>
                        <el-button style="float: right; padding: 3px 0" type="text" v-if="isg" @click="goManage(1,o.hpno)">删帖</el-button>
                        <el-button style="float: right; padding: 3px 0" type="text" v-if="isg" @click="goManage(2,o.hpno)">置顶</el-button>
                        <el-button style="float: right; padding: 3px 0" type="text" v-if="isg" @click="goManage(3,o.hpno)">加精</el-button>
                        <el-button style="float: right; padding: 3px 0" type="text" @click="goTopic(o)">进入贴内</el-button>
                    </div>
                    <div class="text item">
                        <img :src=o.url class="image">
                        <span>{{o.short}}</span>
                    </div>
                </el-card>
                <div style="height:20px;"></div>
            </el-row>
            <el-footer>
                <div class="tmp">
                    我要发帖
                    <topicup v-bind:gpno="mygroup[status].gpno"></topicup>
                </div>
            </el-footer>
        </el-main>
        <el-main v-else>
            <topicd v-bind:o=this.topicO></topicd>
        </el-main>
    </el-container>
</template>

<script>
import topicup from "@/components/topic up.vue"
import topicd from "@/components/topic_detail.vue"
export default {
    data(){
        return{
            usrnm:'用户名阿巴阿巴',
            isg:false,
            topic:[
                [
                {name:'一组的预览',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                ],

                [{name:'二组的预览',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                ],

                [{name:'三组的预览',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                ],

                [{name:'四组的预览',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'组的预览',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                ],

                [{name:'五组的预览',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                {name:'紫罗兰永恒花园',short:'阿巴阿巴阿巴阿巴', url:require('@/assets/anim.jpg'), path: '/proto'},
                ],
            ],
            mygroup:[
                {name:'group1',short:'阿巴阿巴阿巴阿巴', num:1},
                {name:'group2',short:'阿巴阿巴阿巴阿巴', num:2},
                {name:'group3',short:'阿巴阿巴阿巴阿巴', num:3},
                {name:'group4',short:'阿巴阿巴阿巴阿巴', num:4},
                {name:'group5',short:'阿巴阿巴阿巴阿巴', num:5},
            ],
            post:[

            ],
            status:-1,
            isTopic:false,
            topicO:null,}},
    methods:{
        goManage(op,hpno){
          var s;
          s=this.urid;
          let params={op:op,hpno:hpno,username:s};
          this.$http.post('/mana.php',params).then(
              response=>{
                  alert(response.data);
              }
          )
        },
        manage(gpno){
          var s=this.urid;
          if(s=='') alert('请先登录！');
          else{
              let params={gpno:gpno,username:s};
              this.$http.post('/manage.php',params).then(
                  response=>{
                      alert(response.data);
                  }
              )
          }
        },
        change(num){
            this.status = num,
            this.which = num,
            this.isTopic = false
        },
        goTopic(o){
            this.isTopic = true;
            this.topicO = o;
        },
        cardClick(Keypath) {
            this.$router.push({path: Keypath})
        }
    },
    components:{
        topicup,
        topicd
    },
    created() {
        var guro=localStorage.getItem('username');
        var gurt=sessionStorage.getItem('username');
        if(guro!=null) this.urid=guro;
        if(gurt!=null) this.urid=gurt;
        console.log(guro);
        console.log(gurt);
        console.log(this.urid);
        this.usrnm=this.urid;
        let params={op:1,username:this.urid};
        this.$http.post('/getgp.php',params).then(
            response=>{
                console.log(response);
                this.mygroup=[];
                var i;
                var ss=response.data;
                for(i=0;i<ss.length;i++){
                    this.mygroup.push({gpno:ss[i][0],name:ss[i][1],short:ss[i][2]});
                }
            }
        )
        let paramst={op:2,username:this.urid};
        this.$http.post('/getgp.php',paramst).then(
            response=>{
                var ss=(response.data);
                var i;
                var j;
                for(i=0;i<ss.length;i++){
                    var tmp=ss[i];
                    console.log(tmp);
                    var ps=[];
                    for(j=0;j<tmp.length;j++){
                        var url;
                        if(tmp[j][1].indexOf('http')!=-1) url=tmp[j][1];
                         else{
                             url=require('@/static/'+tmp[j][1]);
                         }
                        var isjh;
                        isjh=tmp[j][4];
                        var iszd;
                        iszd=tmp[j][5];
                        if(isjh==1) tmp[j][2]='[精华]'+tmp[j][2];
                        if(iszd==1) tmp[j][2]='[置顶]'+tmp[j][2];
                        ps.push({hpno:tmp[j][0],url:url,name:tmp[j][2],short:tmp[j][3]});
                    }
                    this.post.push(ps);
                }
            }
        )
        this.$http.post('/getgp.php',{op:3,username:this.urid}).then(
            reponse=>{
                this.topic=[];
                var ss=reponse.data;
                var i;
                for(i=0;i<ss.length;i++){
                    this.topic.push({gpno:ss[i][0],name:ss[i][1],short:ss[i][2],url:ss[i][3]});
                }
            }
        )
    },
    updated() {
        var s=this.urid;
        let paramstt={gpno:this.mygroup[this.status].gpno,username:s};
        this.$http.post('/isg.php',paramstt).then(
            response=> {
                console.log(response);
                if(response.data==1) this.isg=true;
                else this.isg=false;
            })
    }
}
</script>

